header {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 8.8rem;
  border-bottom: 2px solid rgba(255, 255, 255, 0.2);
  .container {
    @include flex(row, center, space-between);
  }
  nav {
    display: flex;
    align-items: center;
    ul {
      display: flex;
      align-items: center;
      margin-right: 5.9rem;
      li {
        &:not(:first-child) {
          margin-left: 4.8rem;
        }
        a {
          font-family: "Poppins", sans-serif;
          font-weight: 500;
          font-size: 1.6rem;
          line-height: 125%;
          text-align: right;
          color: $cor36;
          transition: all 0.3s;
          &:hover {
            color: $cor20;
          }
        }
      }
    }
  }

  .btn-mobile {
    display: none;
    margin-left: 4rem;
  }

  @include responsive(1110px) {
    nav {
      ul {
        margin-right: 3rem;
        li {
          &:not(:first-child) {
            margin-left: 2.5rem;
          }
        }
      }
    }
  }

  @include responsive(991px) {
    .logo {
      max-width: 10.8rem;
    }
    nav {
      ul {
        display: none;
      }
    }
    .btn-mobile {
      display: block;
    }
  }

  @include responsive(500px) {
    height: 7.3rem;
    nav {
      .btn-tertiary {
        display: none;
      }
    }
  }
}
